import { Link } from "react-router-dom";
import { useArray } from "../../utils/hooks/use_array";
import { DisplayRoutes } from "../reusable_components/create_booking/display_routes";
import { SearchRouteInputField } from "../reusable_components/create_booking/search_route_input_field";
import { ROUTE_LINKS } from "../../utils/route_links";
import { useEffect, useState } from "react";
import { useCreateBookingLocalStorage } from "../../utils/hooks/use_local_storage_createbooking";
import { LOCAL_STORAGE_KEYS } from "../../utils/local_storage_keys";
import { Button } from "../reusable_components/custom_button";
import { ErrorAlert } from "../reusable_components/alerts/alerts";
import { ThreeDots } from "react-loader-spinner";
import { useRoutesHook } from "../../utils/hooks/use_routes_hook";
import { useComputeDistance } from "../../utils/hooks/use_compute_distance";

export default function ChooseRoutes() {
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const {
    data,
    isComputeDistanceLoading,
    // isComputeDistanceSuccess,
    isComputeDistanceError,
    computeDistanceError,
    computeDistanceAsync,
  } = useComputeDistance(setDisableUserInterface);
  const useArrayOutput = useArray([]);
  const useRoutesOutput = useRoutesHook(useArrayOutput.push);

  const { setCreateBookingObj, getCreateBookingObj } =
    useCreateBookingLocalStorage(LOCAL_STORAGE_KEYS.create_booking);

  useEffect(() => {
    const booking = getCreateBookingObj();
    if (booking.pickup_dropoff_routes)
      useArrayOutput.set(booking?.pickup_dropoff_routes);
  }, []);

  const handleOnClick = async () => {
    if (useArrayOutput.array.length < 1) return;
    const booking = getCreateBookingObj();
    if (booking) {
      try {
        await computeDistanceAsync({
          pickup_dropoff_routes: useArrayOutput.array,
        }).then((response) => {
          // console.log(response);
          setCreateBookingObj({
            ...booking,
            distance: Number(response["distance"]),
            pickup_dropoff_routes: useArrayOutput.array,
          });
          window.location.href = ROUTE_LINKS.chooseadditionalinformation;
        });
      } catch (error) {}
    }
  };

  return (
    <main className="max-w-xl mx-auto my-4 p-6 text-black bg-white">
      <h2 className="text-start text-black pb-6 px-2 font-extrabold text-3xl">
        Choose Pick up and drop off locations
        <p className="text-sm font-normal text-slate-600 pt-2">
          Thank you for choosing Matols logistics services as your go to service
          provider.
        </p>
      </h2>
      <DisplayRoutes
        useArrayOutput={useArrayOutput}
        disableUserInterface={disableUserInterface}
      />
      <SearchRouteInputField
        useRoutesOutput={useRoutesOutput}
        disableUserInterface={disableUserInterface}
        setDisableUserInterface={setDisableUserInterface}
      />

      <div className="px-2 py-4 ">
        {isComputeDistanceError && (
          <ErrorAlert
            className="mb-4"
            secondaryText={`${computeDistanceError.response.data.message} ${computeDistanceError.message}`}
          />
        )}
        <div className="flex flex-row items-center justify-end gap-3">
          <Link
            to={ROUTE_LINKS.createbooking}
            className="px-3 py-2 rounded-md text-yellow-400 bg-black font-bold border-none ring-0"
          >
            Back
          </Link>
          <Button
            disabled={useArrayOutput.array.length < 1}
            className="w-auto"
            onClick={handleOnClick}
          >
            {isComputeDistanceLoading ? (
              <div className="w-full h-full flex justify-center items-center">
                <ThreeDots visible width="40" height="20" color="#000" />
              </div>
            ) : (
              "Select and continue"
            )}
          </Button>
        </div>
      </div>
    </main>
  );
}

