import React, { useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { cn } from "../../../utils/custom_functions";

export const RouteDetailForm = React.forwardRef(
  (
    {
      className,
      route,
      routeIndex,
      updateRoute,
      toggleRouteDetailVisibility,
      disableUserInterface,
      ...props
    },
    ref
  ) => {
    const { register, handleSubmit, reset } = useForm();
    const onSubmit = (data) => {
      const newData = { ...route, ...data };
      console.log(newData);
      updateRoute(routeIndex, { ...route, ...data });
      toggleRouteDetailVisibility();
      reset();
    };
    return (
      <form
        {...props}
        ref={ref}
        className={cn("w-full hidden", className)}
        onSubmit={handleSubmit(onSubmit)}
      >
        <div>
          <input
            {...register("route_detail", {
              disabled: disableUserInterface,
              // required: "Please enter not youd like to convey",
            })}
            type="text"
            name="route_detail"
            placeholder="Please enter details"
            className={cn(
              "text-sm text-slate-800  w-full relative shadow-sm block rounded-lg border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none"
            )}
          />
        </div>
        <div className="w-full flex flex-row justify-end items-center px-2 pt-2">
          <button
            type="submit"
            className="text-green-700 text-sm font-bold hover:text-opacity-60 disabled:pointer-events-none disabled:opacity-60"
          >
            Done
          </button>
        </div>
      </form>
    );
  }
);
