import { useEffect, useRef, useState } from "react";
import { RouteDetailForm } from "./route_detail_form";
import { cn } from "../../../utils/custom_functions";
import { MdCancel } from "react-icons/md";

export function RouteCard({
  routeIndex,
  route,
  updateRoute,
  removeRoute,
  disableUserInterface,
  ...props
}) {
  const [isRouteDetailVisible, setIsRouteDetailVisible] = useState(false);
  // console.log(route, "route number");
  const routeDetailRef = new useRef(null);
  const toggleRouteDetailVisibility = () => {
    const contains = routeDetailRef?.current?.classList;
    if (contains.contains("hidden")) {
      setIsRouteDetailVisible((r) => true);
      contains.remove("hidden");
    } else {
      setIsRouteDetailVisible((r) => false);
      contains.add("hidden");
    }
  };

  return (
    <div {...props}>
      <div className="flex flex-col border border-gray-200 px-3 py-2 rounded-lg ">
        <div className="flex justify-between">
          <div className="flex flex-col">
            {/* <p className="text-xs font-bold text-yellow-400">{route.purpose}</p> */}
            <p className="text-sm font-bold">
              {route.primary_text},{" "}
              <span className="font-normal">{route.secondary_text}</span>
            </p>

            {route.route_detail && (
              <p className="text-gray-500 text-sm">{route.route_detail}</p>
            )}
          </div>
          <div></div>
          <button
            disabled={disableUserInterface}
            onClick={() => removeRoute(routeIndex)}
            className="text-red-700 font-bold ml-3"
          >
            <MdCancel size={20} className="text-red-500" />
          </button>
        </div>
        <RouteDetailForm
          ref={routeDetailRef}
          className="mt-3"
          route={route}
          updateRoute={updateRoute}
          routeIndex={routeIndex}
          toggleRouteDetailVisibility={toggleRouteDetailVisibility}
          disableUserInterface={disableUserInterface}
        />
      </div>
      <div className="w-full flex flex-row justify-end items-center px-2 py-1">
        <button
          disabled={disableUserInterface}
          onClick={toggleRouteDetailVisibility}
          className={cn(
            "text-yellow-400 text-sm font-bold hover:text-opacity-60",
            {
              "text-red-700": isRouteDetailVisible,
            }
          )}
        >
          {isRouteDetailVisible ? "Cancel" : "Add detail"}
        </button>
      </div>
    </div>
  );
}
